/**
 * ----------------------------------------
 * animation scale-up-center
 * ----------------------------------------
 */
 @-webkit-keyframes scale-up-center {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  @keyframes scale-up-center {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  
@media screen and (max-width: 700px) {
    .section_padding {
        padding: 4rem;
    }
    .section_margin {
        margin: 4 rem;
    }
}

@media screen and (max-width: 550px) {
    .section_padding {
        padding: 4rem 2rem;
    }
    .section_margin {
        margin: 4 rem 2rem;
    }
}